import React from 'react';
import { Panel, Group, Header, SimpleCell, InfoRow } from '@vkontakte/vkui';
import { lastScheduleUpdate } from '../Database';

const Information = props => {
    return (
        <Panel id={props.id}>
            <Group header={<Header mode="secondary">Техническая информация</Header>}>
                <SimpleCell multiline>
                    <InfoRow header="Версия приложения">1.0.4</InfoRow>
                </SimpleCell>
                <SimpleCell multiline>
                    <InfoRow header="Последнее обновление расписания студентов">{lastScheduleUpdate()}</InfoRow>
                </SimpleCell>
            </Group>
        </Panel>
    );
}

export default Information;