import studentSchedule from './database/studentSchedule.json';

export function isEvenWeek() {
   let year = new Date().getFullYear(); 
   let month = new Date().getMonth(); 
   let today = new Date(year, month, 0).getTime(); 
   let now = new Date().getTime(); 
   let week = Math.round((now - today) / (1000 * 60 * 60 * 24 * 7)); 
   if (week % 2 === 0) { 
      return false;
   } else { 
      return true;
   }
 }

export function currentPeriod(form) {
   if (form == "Очная") {
      return studentSchedule.fullTime.period;
   } else if (form == "Очно-заочное") {
      return studentSchedule.partTime.period;
   } else if (form == "Заочное") {
      return studentSchedule.extramural.period;
   }
}

export function lastScheduleUpdate() {
   let date = new Date(studentSchedule.lastUpdate);
   let days = date.getDate();
   let month = date.getMonth() + 1;
   let year = date.getFullYear();
   let hours = date.getHours();
   let minutes = date.getMinutes();
   let seconds = date.getSeconds();
   if (month < 10) month = "0" + month;
   if (days < 10) days = "0" + days;
   if (hours < 10) hours = "0" + hours;
   if (minutes < 10) minutes = "0" + minutes;
   if (seconds < 10) seconds = "0" + seconds;
   return (days + "." + month + "." + year + " | " + hours + ":" + minutes + ":" + seconds);
}

export function findGroups(form, level, faculty) {
   let groups = [];
   let options = [];

   function filterGroups(items) {
      if (!level && faculty) {
         return items.filter(x => x.faculty === faculty);
      }
      if (level && !faculty) {
         return items.filter(x => x.level === level);
      }
      if (level && faculty) {
         return items.filter(x => x.level === level).filter(x => x.faculty === faculty);
      }
      if (!level && !faculty) {
         return items;
      }
   }

   if (form == "Очная") {
      groups = filterGroups(studentSchedule.fullTime.groups);
   } else if (form == "Очно-заочное") {
      groups = filterGroups(studentSchedule.partTime.groups);
   } else if (form == "Заочное") {
      groups = filterGroups(studentSchedule.extramural.groups);
   }

   for (let i = 0; i < groups.length; i++) {
      options.push({ label: groups[i].group, value: groups[i].group })
   }

   return options;
}

export function findSchedule(form, group) {
   if (form == "Очная") {
      return studentSchedule.fullTime.groups.find(x => x.group === group);
   } else if (form == "Очно-заочное") {
      return studentSchedule.partTime.groups.find(x => x.group === group);
   } else if (form == "Заочное") {
      return studentSchedule.extramural.groups.find(x => x.group === group);
   }
   return false;
}