import React, { useState, useEffect } from 'react';
import bridge from "@vkontakte/vk-bridge";
import { MiniInfoCell, Panel, Select, FormItem, Card, Header, Group, CardGrid, Separator, Spacing, RichCell } from '@vkontakte/vkui';
import { Icon20CalendarOutline, Icon20EducationOutline } from '@vkontakte/icons';
import { isEvenWeek, currentPeriod, findGroups, findSchedule } from '../Database';

const Schedule = props => {
    const [form, setForm] = useState('очная');
    const [level, setLevel] = useState();
    const [faculty, setFaculty] = useState();
    const [group, setGroup] = useState();

    function storageSet(value) {
        bridge.send('VKWebAppStorageSet', { 
            key: 'studentSchedule',
            value: JSON.stringify([form, level, faculty, value]),
        });
    }

    useEffect(() => {
        bridge
            .send('VKWebAppStorageGet', { keys: ['studentSchedule']})
            .then((data) => {
                try {
                    setForm(JSON.parse(data.keys[0].value)[0]);
                    setLevel(JSON.parse(data.keys[0].value)[1]);
                    setFaculty(JSON.parse(data.keys[0].value)[2]);
                    setGroup(JSON.parse(data.keys[0].value)[3]);
                } catch {
                    return;
                }
            });
    }, []);

    let groupData = findSchedule(form, group);

    return (
        <Panel id={props.id}>
            <Group>
                <MiniInfoCell mode="accent" before={<Icon20CalendarOutline/>}>{isEvenWeek() ? "Сейчас идёт чётная неделя (нижние пары)":"Сейчас идёт нечётная неделя (верхние пары)"}</MiniInfoCell>
                <MiniInfoCell mode="accent" before={<Icon20EducationOutline/>}>Текущий период: {currentPeriod(form)}</MiniInfoCell>
            </Group>
            <Group>
                <FormItem top="Форма обучения">
                    <Select multiline
                        value={form}
                        onChange={(e) => setForm(e.target.value)}
                        options={[
                            { label: 'Очная', value: 'Очная' },
                            { label: 'Очно-заочная', value: 'Очно-заочное' },
                            { label: 'Заочная', value: 'Заочное' },
                        ]}
                    />
                </FormItem>
                <FormItem top="Уровень образования">
                    <Select multiline
                        value={level}
                        onChange={(e) => setLevel(e.target.value)}
                        placeholder="Выберите уровень образования"
                        options={[
                            { label: 'Колледж', value: 'Колледж' },
                            { label: 'Магистр', value: 'Магистр' },
                            { label: 'Аспирант', value: 'Аспирант' },
                            { label: 'Специалист', value: 'Специалист' },
                            { label: 'Бакалавр', value: 'Бакалавр' },
                        ]}
                    />
                </FormItem>
                <FormItem top="Факультет / Институт">
                    <Select multiline
                        value={faculty}
                        onChange={(e) => setFaculty(e.target.value)}
                        placeholder="Выберите факультет или институт"
                        options={[
                            { label: 'Механико-технологический факультет', value: 'Механико-технологический факультет' },
                            { label: 'Политехнический колледж', value: 'Политехнический колледж' },
                            { label: 'Учебно-научный институт транспорта', value: 'Учебно-научный институт транспорта' },
                            { label: 'Учебно-научный технологический институт', value: 'Учебно-научный технологический институт' },
                            { label: 'Факультет информационных технологий', value: 'Факультет информационных технологий' },
                            { label: 'Факультет отраслевой и цифровой экономики', value: 'Факультет отраслевой и цифровой экономики' },
                            { label: 'Факультет энергетики и электроники', value: 'Факультет энергетики и электроники' },
                        ]}
                    />
                </FormItem>
                <FormItem top="Группа">
                    <Select multiline
                        value={group}
                        onChange={(e) => {
                            setGroup(e.target.value);
                            storageSet(e.target.value);
                        }}
                        placeholder="Выберите группу"
                        options={findGroups(form, level, faculty)}
                    />
                </FormItem>
            </Group>
            {groupData && Object.keys(groupData.schedule).map((day, m) => (
                <Group header={<Header mode="primary" size="large">{day}</Header>}>
                    <CardGrid size="l">
                        {Object.keys(groupData.schedule[day]).map((lessons, m) => (
                            <Card mode="shadow" >
                                {groupData.schedule[day][lessons].week==="Всегда" && (
                                    <RichCell multiline
                                        disabled={true}
                                        before={groupData.schedule[day][lessons].time}
                                        text={groupData.schedule[day][lessons].type}
                                        caption={groupData.schedule[day][lessons].teacher}
                                        after={groupData.schedule[day][lessons].classroom}
                                    >{groupData.schedule[day][lessons].title}</RichCell>
                                )}
                                {groupData.schedule[day][lessons].week==="Чётная" && (
                                    <RichCell multiline
                                        disabled={true}
                                        before={groupData.schedule[day][lessons-1].time}
                                        text={groupData.schedule[day][lessons-1].type}
                                        caption={groupData.schedule[day][lessons-1].teacher}
                                        after={groupData.schedule[day][lessons-1].classroom}
                                    >{groupData.schedule[day][lessons-1].title}</RichCell>
                                )}
                                {groupData.schedule[day][lessons].week==="Чётная" && (
                                    <Spacing size={12}>
                                        <Separator />
                                    </Spacing>
                                )}                             
                                {groupData.schedule[day][lessons].week==="Чётная" && (
                                    <RichCell multiline
                                        disabled={true}
                                        before={groupData.schedule[day][lessons].time}
                                        text={groupData.schedule[day][lessons].type}
                                        caption={groupData.schedule[day][lessons].teacher}
                                        after={groupData.schedule[day][lessons].classroom}
                                    >{groupData.schedule[day][lessons].title}</RichCell>
                                )}
                            </Card>
                        ))}
                    </CardGrid>
                </Group>
            ))}
        </Panel>
    );
}

export default Schedule;