import React, { useState } from 'react';
import { View, AdaptivityProvider, AppRoot, Group, SplitCol, SimpleCell, Avatar, Tabs, TabsItem, ConfigProvider, HorizontalScroll, SplitLayout } from '@vkontakte/vkui';
import { Icon16EducationOutline, Icon16InfoOutline } from '@vkontakte/icons';
import '@vkontakte/vkui/dist/vkui.css';

import Schedule from './panels/Schedule';
import Information from './panels/Information';

import logo from './images/logo.png';

const App = () => {
	const [activePanel, setActivePanel] = useState('schedule');

	return (
		<ConfigProvider>
			<AdaptivityProvider>
				<AppRoot>
      				<SplitLayout>
        				<SplitCol>
							<Group>
								<SimpleCell disabled={true} before={<Avatar src={logo} size={32} />}>БГТУ Расписание</SimpleCell>
								<Tabs mode="accent"	layoutFillMode="auto" withScrollToSelectedTab scrollBehaviorToSelectedTab="center">
									<HorizontalScroll arrowSize="m">
										<TabsItem
											before={<Icon16EducationOutline/>}
											selected={activePanel === 'schedule'}
											onClick={() => setActivePanel('schedule')}
										>Расписание студентов</TabsItem>
										<TabsItem
											before={<Icon16InfoOutline/>}
											selected={activePanel === 'information'}
											onClick={() => setActivePanel('information')}
										>О приложении</TabsItem>
									</HorizontalScroll>
								</Tabs>
							</Group>
							<ConfigProvider transitionMotionEnabled={false}>
								<View activePanel={activePanel}>
									<Schedule id="schedule" />
									<Information id="information" />
								</View>
							</ConfigProvider>
       					</SplitCol>
      				</SplitLayout>
    			</AppRoot>
			</AdaptivityProvider>
		</ConfigProvider>
	);
}

export default App;
